  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

section {
    position: relative;
    width: 100%;
    min-height: 100vh;
    padding: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #fff0!important;
}

header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 20px 100px;
    display: flex;
    justify-content: space-between;
    align-items: top;
}

header .logo {
    position: relative;
    max-width: 80px;
}

header ul {
    position: relative;
    display: flex;
}

header ul li {
    list-style: none;
}

header ul li a {
    display: inline-block;
    color: #333;
    font-weight: 400;
    margin-left: 40px;
    text-decoration: none;
    padding: 8px 20px;
}
header button {
    display: inline-block;
    margin-left: 40px;
    padding: 8px 20px;
    background: #320171;
    color: rgb(255, 255, 255);
    border-radius: 40px;
    font-weight: 600;
    display: flex;
    text-decoration: none;
    align-items: flex-start;
    border-style: none;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}
 button {
    display: inline-block;
    margin-left: 40px;
    padding: 8px 20px;
    background: #320171;
    color: rgb(255, 255, 255);
    border-radius: 40px!important;
    font-weight: 600;
    display: flex;
    text-decoration: none;
    align-items: center;
    border-style: none;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}

.hijo {
    display: flex;
    justify-content: center;    
    margin: 0 auto;
  }
button:hover{
    -webkit-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2);
}

header .textBox c {
    display: inline-block;
    margin-top: 20px;
    padding: 8px 20px;
    background: #320171;
    color: #fff;
    border-radius: 40px;
    font-weight: 500;
    letter-spacing: 1px;
    text-decoration: none;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}

.content {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-content: center;
}

.content .textBox {
    position: relative;
    max-width: 600px;
    margin-top: 10rem;
}

.content .textBox h2 {
    color: #333;
    font-size: 4rem;
    line-height: 1.4em;
    font-weight: 500;
}

.content .textBox h2 span {
    color: #320171;
    font-size: 1.2em;
    font-weight: 900;
} 

.content .textBox p {
    color: #333;
}

.content .textBox a {
    display: inline-block;
    margin-top: 20px;
    padding: 8px 20px;
    background: #320171;
    color: #fff;
    border-radius: 40px;
    font-weight: 500;
    letter-spacing: 1px;
    text-decoration: none;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}
a:hover{
    -webkit-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2);
}

.content .imgBox {
    widows: 600px;
    display: flex;
    justify-content: flex-end;
    padding-right: 50px;
    margin-top: 50px;
}

.content .imgBox img {
    max-width: 340px;
}

.thumb {
    position: absolute;
    left: 50%;
    bottom: 20px;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    display: flex;
    transition: 0.5s;
}

.thumb li {
    list-style: none;
    display: inline-block;
    margin: 0 20px;
    cursor: pointer;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}

.thumb li:hover {
    -webkit-transform: translateY(-35px) scale(1.2, 1.2);
            transform: translateY(-35px) scale(1.2, 1.2);
} 

.thumb li img {
    max-width: 60px;
}

.sci {
    position: absolute;
    top: 50%;
    right: 30px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    
    
}

.sci li {
    list-style: none;
}

.sci li a {
    display: inline-block;
    margin: 5px 0;
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    -webkit-filter: invert(1);
            filter: invert(1);
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}
.sci li:hover {
    -webkit-transform:  scale(1.2, 1.2);
            transform:  scale(1.2, 1.2);
} 

.circle {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #320171;
    -webkit-clip-path: circle(600px at right 800px);
            clip-path: circle(600px at right 800px);
}

.modal-login {		
    color: #636363;
    width: 350px;
    margin: 80px auto 0;
}
.modal-login .modal-content {
    padding: 20px;
    border-radius: 5px;
    border: none;
}
.modal-login .modal-header {
    border-bottom: none;   
    position: relative;
    justify-content: center;
}
.modal-login h4 {
    text-align: center;
    font-size: 26px;
    margin: 30px 0 -15px;
}
.modal-login .form-control:focus {
    border-color: #320171;
}
.modal-login .form-control, .modal-login .btn {
    min-height: 40px;
    border-radius: 3px; 
}
.modal-login .close {
    position: absolute;
    top: -5px;
    right: -5px;
}	
.modal-login .modal-footer {
    background: #ecf0f1;
    border-color: #dee4e7;
    text-align: center;
    justify-content: center;
    margin: 0 -20px -20px;
    border-radius: 5px;
    font-size: 13px;
}
.modal-login .modal-footer a {
    color: #999;
}		
.modal-login .avatar {
    position: absolute;
    margin: 0 auto;
    left: 0;
    right: 0;
    top: -70px;
    width: 95px;
    height: 95px;
    border-radius: 50%;
    z-index: 9;
    background: #320171;
    padding: 15px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.1);
}
.modal-login .avatar img {
    width: 100%;
}
.modal-login .btn {
    color: #fff;
    border-radius: 40px;
    background: #320171;
    text-decoration: none;
    transition: all 0.4s;
    line-height: normal;
    border: none;
}
.modal-login .btn:hover, .modal-login .btn:focus {
    background: #210941;
    outline: none;
}
.trigger-btn {
    display: inline-block;

}
.modal-dialog{
    position: fixed!important;
    top: -10%!important;
    right: 60%!important;
}
.modal-header {
    border-bottom: 1px solid rgba(0, 0, 0, 0)!important;
  }
  .modal-content {
    
    width: -webkit-max-content !important;
    
    width: max-content !important;
    background-color: rgba(0, 0, 0, 0) !important;
    border: 1px solid rgba(0, 0, 0, 0)!important;
  
  }
  .modal-body{
      position:center !important;
  }
  .modal.show .modal-dialog {
    -webkit-transform: none;
            transform: none;
    width: -webkit-max-content !important;
    width: max-content !important;
  }
  .modal
  {
    padding: 1rem;
    
  }
